//-----------------------------------------------------------------------------
// Imports
//-----------------------------------------------------------------------------
import { Sheets, SheetActive, SheetCells, SheetColumns, SheetColumnTypes, SheetClipboard, SheetFilters, SheetGroups, SheetRows, SheetSorts } from '@app/state/sheet/types'
import { 
  SheetActions, 
  LOAD_SHEET, UPDATE_SHEET, 
  UPDATE_SHEET_ACTIVE,
  UPDATE_SHEET_CELL, UPDATE_SHEET_CELLS,
  UPDATE_SHEET_CLIPBOARD,
  UPDATE_SHEET_COLUMN, UPDATE_SHEET_COLUMNS,
  UPDATE_SHEET_FILTER, UPDATE_SHEET_FILTERS,
  UPDATE_SHEET_GROUP, UPDATE_SHEET_GROUPS,
  UPDATE_SHEET_ROW, UPDATE_SHEET_ROWS,
  UPDATE_SHEET_SORT, UPDATE_SHEET_SORTS
} from '@app/state/sheet/actions'

//-----------------------------------------------------------------------------
// Types
//-----------------------------------------------------------------------------
export interface SheetState {
  active: SheetActive
  clipboard: SheetClipboard
  sheets: Sheets
  cells: SheetCells
  columns: SheetColumns
  columnTypes: SheetColumnTypes
  filters: SheetFilters
  groups: SheetGroups
  rows: SheetRows
  sorts: SheetSorts
}

//-----------------------------------------------------------------------------
// Default State
//-----------------------------------------------------------------------------
const columnTypesFromServer = initialData && initialData.columnTypes ? initialData.columnTypes : {}
export const defaultSheetState: SheetState = {
  active: {
    columnRenamingId: null
  },
  clipboard: {
    sheetId: null,
    cutOrCopy: null,
    selections: null
  },
  sheets: null,
  cells: null,
  columns: null,
  columnTypes: {
    STRING: {
      id: 'STRING',
      organizationId: null,
      userId: null,
      sheetId: null,
      name: 'Text',
      cellType: 'STRING',
      data: null
    },
    NUMBER: {
      id: 'NUMBER',
      organizationId: null,
      userId: null,
      sheetId: null,
      name: 'Number',
      cellType: 'NUMBER',
      data: null
    },
    BOOLEAN: {
      id: 'BOOLEAN',
      organizationId: null,
      userId: null,
      sheetId: null,
      name: 'Checkbox',
      cellType: 'BOOLEAN',
      data: null
    },
    DATETIME: {
      id: 'DATETIME',
      organizationId: null,
      userId: null,
      sheetId: null,
      name: 'Date',
      cellType: 'DATETIME',
      data: null
    },
    PHOTOS: {
      id: 'PHOTOS',
      organizationId: null,
      userId: null,
      sheetId: null,
      name: 'Photos',
      cellType: 'PHOTOS',
      data: null
    },
    FILES: {
      id: 'FILES',
      organizationId: null,
      userId: null,
      sheetId: null,
      name: 'Files',
      cellType: 'FILES',
      data: null
    },
    ...columnTypesFromServer
  },
  filters: null,
  groups: null,
  rows: null,
  sorts: null
}

//-----------------------------------------------------------------------------
// Reducers
//-----------------------------------------------------------------------------
export const userReducer = (state: SheetState = defaultSheetState, action: SheetActions): SheetState => {

	switch (action.type) {
    
		case LOAD_SHEET: {
			const { cells, columns, filters, groups, rows, sheet, sorts } = action
			return {
        ...state,
        sheets: { ...state.sheets, [sheet.id]: sheet },
        cells: { ...state.cells, ...cells },
        columns: { ...state.columns, ...columns },
        filters: { ...state.filters, ...filters },
        groups: { ...state.groups, ...groups },
        rows: { ...state.rows, ...rows },
        sorts: { ...state.sorts, ...sorts }
			}
		}
      
		case UPDATE_SHEET: {
      const { sheetId, updates } = action
      return {
        ...state,
        sheets: {...state.sheets, 
          [sheetId]: { ...state.sheets[sheetId], ...updates}
        }
      }
		}

		case UPDATE_SHEET_ACTIVE: {
			const { updates } = action
			return {
        ...state,
        active: { ...state.active, ...updates }
      }
		}

		case UPDATE_SHEET_CELL: {
			const { cellId, updates } = action
			return {
        ...state,
        cells: { ...state.cells,
          [cellId]: { ...state.cells[cellId], ...updates }
        }
      }
		}

		case UPDATE_SHEET_CELLS: {
			const { nextSheetCells } = action
			return {
        ...state,
        cells: nextSheetCells
      }
		}

		case UPDATE_SHEET_CLIPBOARD: {
			const { nextSheetClipboard } = action
			return {
        ...state,
        clipboard: nextSheetClipboard
      }
		}

		case UPDATE_SHEET_COLUMN: {
			const { columnId, updates } = action
			return {
        ...state,
        columns: { ...state.columns,
          [columnId]: { ...state.columns[columnId], ...updates}
        }
      }
		}

		case UPDATE_SHEET_COLUMNS: {
			const { nextSheetColumns } = action
			return {
        ...state,
        columns: nextSheetColumns
      }
		}

		case UPDATE_SHEET_FILTER: {
			const { filterId, updates } = action
			return {
        ...state,
        filters: { ...state.filters,
          [filterId]: { ...state.filters[filterId], ...updates}
        }
      }
		}

		case UPDATE_SHEET_FILTERS: {
			const { nextSheetFilters } = action
			return {
        ...state,
        filters: nextSheetFilters
      }
		}

		case UPDATE_SHEET_GROUP: {
			const { groupId, updates } = action
			return {
        ...state,
        groups: { ...state.groups,
          [groupId]: { ...state.groups[groupId], ...updates}
        }
      }
		}

		case UPDATE_SHEET_GROUPS: {
			const { nextSheetGroups } = action
			return {
        ...state,
        groups: nextSheetGroups
      }
		}

		case UPDATE_SHEET_ROW: {
			const { rowId, updates } = action
			return {
        ...state,
        rows: { ...state.rows,
          [rowId]: { ...state.rows[rowId], ...updates}
        }
      }
		}

		case UPDATE_SHEET_ROWS: {
			const { nextSheetRows } = action
			return {
        ...state,
        rows: nextSheetRows
      }
		}

		case UPDATE_SHEET_SORT: {
			const { sortId, updates } = action
			return {
        ...state,
        sorts: { ...state.sorts,
          [sortId]: { ...state.sorts[sortId], ...updates}
        }
      }
		}

		case UPDATE_SHEET_SORTS: {
			const { nextSheetSorts } = action
			return {
        ...state,
        sorts: nextSheetSorts
      }
		}

		default:
			return state
	}
}

export default userReducer
